.topNav 
{
    display:flex;
    align-items:center;
    padding:2%;
	margin:0 1em;
}
.topNav .logo{
    width:25%;
    height:100%;
    display:flex;
    justify-content: center;
    align-items:center;
    margin: 0;
}
.logo svg{
    width:100%;
    height:100%;
}
.logo svg{
    height:100%;
}
.menu{
   display:flex;
   justify-content:space-evenly;
   flex:1;
}
.menu__links{
	display:flex;
	flex:0.8;
    justify-content:center;
}
.menu a{
    margin:0 2%;
    text-decoration:none;
    font-size:1.75em;
    font-family:'Roboto Condensed';
    color:#192028;
    
}
.menu a.active_menu_item,.menu a:hover{
	border-bottom:3px solid #00bcd5;

}
.topNav .toggle-div{
    display:flex;
    align-items: center;
}
.toggle-div .icon{
    width:45px;
    height:45px;
    color:#00bcd5;
}


@media only screen and (max-width: 837px) {
   .menu{
       display:none;
       
   }
   .topNav .logo{
       width:50%;
   }
   .topNav .toggle-div{
    display:flex;
    justify-content: flex-end;
    flex:1;
}

  }