*{
    box-sizing: border-box;
    margin: 2px;
  
}


.footer{
    background-color:#00bcd5;
    width: 100%;
     height: auto;
     font-weight: 400px;
     padding:2rem 4rem;
     color: whitesmoke;
   
}
.f_contain{
    display: flex;
    align-items:flex-start;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
    padding-top: 1rem;
    
}


.logo{
   width: 100%;
   height:100%;

}

.f1{ 
    width: 25%;
}
.f3 li{
    display:flex;
}
.f3{ 
    width: 200px;
}

.f_image{
    max-width: 90%;
   height: auto;
}
.f_icn{
    color: whitesmoke;
    font-size: 25px;
    margin-right: 5px;
}
.footer ul,.footer a{
     text-decoration:none ;
     list-style-type: none;
     color: whitesmoke;
    text-transform: capitalize;
}
.f_heading{
   padding-bottom: 2rem;
    font-size: 30px;
    text-transform: uppercase;
    justify-content: center;
}
.f_list{
    margin: 1rem 0;
  font-size: 22px;
}
.f_ul{
    padding: 0;
}
.social{
    margin-top: 2rem;
    display: flex;
    align-items:center;
    justify-content: space-between;
    flex-wrap: wrap;
border-radius: 15px;
  color: black;
    background-color: white;
    padding: 0 2rem;
    height: auto;
    box-shadow: 5px 10px 18px rgb(201, 206, 214);
    padding: .5rem 0;
}
.social ul li{
  color:black;
display: inline;
font-size: 30px;
}

.f_contain ul a{
    margin-left: 1rem;
}
.s_icon{
    transition:.2s ease-in-out;
}
.s_icon:hover{
    background-color: rgb(16, 235, 205);
    border-radius:50%;
  
}
.la-facebook{
    color: rgba(70, 93, 221, 0.712);
    
}
.la-twitter{
    color: rgba(28, 108, 122, 0.726);
}
.la-instagram{
    color: rgb(223, 17, 86);
}
.la-linkedin{
    color: rgba(16, 147, 235, 0.767);
}

.copyright{
    display: inline-block;
}

/*media queries*/
@media (max-width:950px){
  
    .f3{
        display: inline;
        width: 100%;
        text-align: center;
        
    }
    .f_contain{
        font-size: 10px;
    }
}

@media (max-width:1000px)
{
    .f_contain{
        height: auto;
    }
 
    .logo{
        display: inline;
        text-align: center;
        width: 100%;
        margin-bottom: 20px;
    }
    .f3{
        display: inline;
    }
  .social {
      justify-content: center;
      
  }
  .copyright{
      text-align: center;
  }
  
    
}