.toggle-button{
	height:30px;
	background:transparent;
	cursor:pointer;
	border:none;
	display:flex;
	flex-direction:column;
	justify-content:space-around;
	
}
.toggle-button:focus{
	outline:none;
	
}	
.toggle-button__line{
	width:30px;
	height:2px;
	background:#00bcd5;

}
.toggle-button__line1{
	width:80px;
}
.toggle-button__line2{
	width:60px;
}
.toggle-button__line3{
	width:40px;
}